.enterprise {
  padding-top: 72px;
  padding-bottom: 72px;
  background-color: var(--grey-100);

  &__title {
    margin-bottom: 48px;
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 412px;
    gap: 24px;
    margin-bottom: 32px;
  }

  &__card {
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    box-shadow: var(--shadow-4);
    background-color: var(--base-white);
    height: 100%;

    &-img {
      width: 100%;
      height: 128px;
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;

      img {
        width: 100%;
        height: 100%;
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
      }
    }

    &-box {
      padding: 24px;
      display: flex;
      flex-direction: column;
      height: 100%;
    }

    &-content {
      margin-bottom: 10px;
    }

    &-title {
      font-weight: 600;
      font-size: 24px;
      line-height: 121.9%;
      margin-bottom: 12px;
    }

    &-descr {
      font-size: 18px;
      line-height: 156%;
      color: var(--grey-500);
    }

    &-btn {
      margin-top: auto;
    }
  }

  &__btn {
    text-align: center;
  }
}
