.industries {
  padding-top: 72px;
  padding-bottom: 72px;

  &__title {
    margin-bottom: 12px;
  }

  &__descr {
    margin-bottom: 48px;
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;
  }

  &__card {
    padding: 24px;
    background-color: var(--base-white);
    border: 2px solid var(--grey-200);
    border-radius: 24px;
    transition: background-color 0.3s ease-in, border 0.3s ease-in;

    &:hover {
      background-color: var(--grey-100);
      border: 2px solid var(--grey-100);
    }
  }

  &__card-img {
    width: 48px;
    height: 48px;
    margin-bottom: 24px;
  }

  &__card-title {
    margin-bottom: 12px;
  }
}
