.experts {
  padding-top: 72px;
  padding-bottom: 72px;
  background: var(--base-black);
  background: radial-gradient(63.11% 267.11% at 50% 267.11%, #00E3AA 0%, #1DE9E1 28.12%, #000000 100%);

  &__row {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;
  }

  &__item {
    &-title {
      font-weight: 600;
      font-size: 48px;
      line-height: 121.9%;
      color: var(--base-white);
    }
    hr {
      width: 96px;
      margin: 12px 0;
      padding: 0;
      height: 0;
      border: none;
      border-top: 2px solid var(--cyan-400);
    }
    &-descr {
      color: var(--grey-300);
    }
  }
}
