.interface {
  padding-top: 24px;
  padding-bottom: 0;
  background-color: var(--grey-100);

  &__title {
    margin-bottom: 12px;
  }

  &__descr {
    margin-bottom: 48px;
  }

  &__img {
    max-width: 1062px;
    height: 512px;
    margin-left: auto;
    margin-right: auto;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;

    img {
      border-top-left-radius: 25px;
      border-top-right-radius: 25px;
    }
  }
}
