.footer {
  &__cta {
    width: 100%;
    background: linear-gradient(to bottom, var(--base-white) 50%, var(--base-black) 50%);

    &-wrapper {
      position: relative;
      margin: 0 auto;
      padding: 72px;
      max-width: 1076px;
      border-radius: 24px;
      background-color: var(--base-grey);
      z-index: 0;

      &::before {
        position: absolute;
        content: "";
        top: 0px;
        right: 0px;
        width: 480px;
        height: 100%;
        border-top-right-radius: 24px;
        border-bottom-right-radius: 24px;
        background-image: url('../img/footer/footer__cta-img.png');
        background-repeat: no-repeat;
        z-index: -1;
      }
    }

    &-content {
      max-width: 488px;
      z-index: 100;
    }

    &-title {
      color: var(--base-white);
      margin-bottom: 12px;
    }

    &-descr {
      color: var(--grey-300);
      margin-bottom: 33px;
    }
  }

  &__wrapper {
    padding-top: 48px;
    padding-bottom: 32px;
    background-color: var(--base-black);
  }

  &__nav {
    max-width: 1076px;
    min-height: 294px;
    margin: 0 auto;
    margin-bottom: 48px;
    display: flex;
  }

  &__nav-column:not(:last-child) {
    margin-right: 92px;
  }

  &__nav-title {
    font-weight: 500;
    font-size: 24px;
    line-height: 117%;
    color: var(--base-white);
    margin-bottom: 12px;
  }

  &__nav-item {
    margin-bottom: 12px;
  }

  &__nav-link {
    font-weight: 400;
    font-size: 18px;
    line-height: 156%;
    color: var(--grey-500);
  }

  &__nav-item--fluid {
    margin-bottom: 24px;
  }

  &__nav-link--fluid {
    font-weight: 500;
    font-size: 24px;
    line-height: 117%;
    color: var(--base-white);
  }

  .logo {
    margin-right: 26px;
  }

  &__info {
    max-width: 1076px;
    margin: 0 auto;
    display: flex;
    align-items: center;
  }

  &__address, &__phone {
    font-weight: 400;
    font-size: 18px;
    line-height: 156%;
    color: var(--grey-500);
  }

  &__address {
    max-width: 522px;
    margin-right: auto;
  }

  &__phone {
    max-width: 194px;

    &:not(:last-child) {
      margin-right: auto;
    }
  }

  &__phone-link {
    font-weight: 400;
    font-size: 18px;
    line-height: 156%;
    color: var(--grey-500);
    transition: color 0.3s ease-in;

    &:hover {
      color: var(--cyan-400);
    }
  }
}
